.page-header {
    height: 65px;
}

.page-header__wrapper {
  width: 320px;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: $tablet-width) {
  .page-header {
    height: 85px;
    margin: 0;
  }

  .page-header__wrapper {
    width: 768px;
    margin: 0 auto;
    display: block;
  }

  .page-header__logo-image,
  .page-header__title {
    float: left;
  }

  .main-nav {
    float: right;
  }
}

@media (min-width: $desktop-width) {
  .page-header {
    height: 130px;
  }

  .page-header__wrapper {
    width: 1200px;
  }

  .main-nav {
    width: 50%;
  }

}
